// pages / dashboard / security

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
import SecurityCamera from '../../../components/dashboard/security/SecurityCamera';


class Security extends React.Component {
    render() {
        if (this.props.user.loggedIn) {
            return (
                <span>

                    {/* Start of Helmet */}
                    <Helmet title="Security Cameras" meta={[
                        {
                            name: 'description',
                            content: 'Security Cameras'
                        }, {
                            name: 'keywords',
                            content: 'Security Cameras'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-10">
                            <h2>Security Cameras</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Security Cameras</strong>
                                </li>
                            </ol>
                        </div>
                        <div className="col-lg-2">
                        </div>
                    </div>
                    {/* End of Heading */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content  animated fadeInRight">
                        <div className="row">
                            <div className="col-md-12">
                                <SecurityCamera name="Guard House" lat="3.1582228" lng="101.7109321" heading="20" pitch="-18" />
                            </div>

                            <div className="col-md-12">
                                <SecurityCamera name="Garden Park" lat="3.1561571" lng="101.7143083" heading="-135" pitch="-10" />
                            </div>

                            <div className="col-md-12">
                                <SecurityCamera name="Car Park" lat="3.1471622" lng="101.7134411" heading="130" pitch="-10" />
                            </div>
                        </div>
                    </div>
                    {/* End of Content */}

                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(Security);



