// components / dashboard / security / SecurityCamera

// Modules
import React from "react";
import {connect} from 'react-redux';


class SecurityCamera extends React.Component {
    componentDidMount() {
        let fenway = new google.maps.LatLng(this.props.lat, this.props.lng);
        let panorama = new google.maps.StreetViewPanorama(document.getElementById(this.props.name), {
            position: fenway,
            zoom: 0,
            pov: {
                heading: Number(this.props.heading),
                pitch: Number(this.props.pitch)
            }
        });
    }

    render() {
        return (
            <div className="ibox ">
                <div className="ibox-title">
                    <h2>{this.props.name}</h2>
                </div>
                <div className="ibox-content">
                    <div className="google-map" id={this.props.name} style={{height: '500px'}}></div>
                </div>
            </div>
        );
    }
};

const mapStateToProps = (state) => {
    return {};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(SecurityCamera);



